/* Ïª®ÌÖåÏù¥ÎÑà Ï†ÑÏ≤¥ */
.player-container {
  max-width: 960px;
  margin: 40px auto;
  padding-top: 0px; /* ÏÉÅÎã® Ìå®Îî©Îßå 5px */
  padding-right: 20px;
  padding-bottom: 20px;
  padding-left: 20px;
  font-size: 20px;
  text-align: center;
  font-family: "Noto Sans KR", "Segoe UI", sans-serif;
  background-color: #f9fafc;
}

/* ÌÖåÏù¥Î∏î */
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.player-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 30px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.06);
  border-radius: 12px;
  overflow: hidden;
  background-color: #ffffff;
}

/* Ìó§Îçî */
.player-container th {
  background-color: #2980b9;
  color: white;
  font-size: 22px;
  padding: 16px;
  border: 1px solid #ddd;
}

/* ÏÖÄ */
.player-container td {
  border: 1px solid #e0e0e0;
  padding: 24px 12px;
  font-size: 20px;
  color: #2c3e50;
  transition: background 0.2s;
}

/* Ìñâ hover */
.player-container tr:hover td {
  background-color: #f2f9ff;
}

/* ÎßÅÌÅ¨ Ïä§ÌÉÄÏùº */
.player-link {
  color: #2c3e50;
  text-decoration: none;
}

.player-link:hover {
  text-decoration: underline;
}

/* Ï†ÑÏ≤¥ ÏÑºÌÑ∞ Ï†ïÎ†¨ Ïú†ÏßÄ */
td,
th {
  text-align: center;
  vertical-align: middle;
}

/* Ïó¨Ïú† Í≥µÍ∞ÑÏö© empty div */
.empty_space {
  height: 30px;
}

/* ÏïàÎÇ¥ Î¨∏Íµ¨ */
.player-container h4 {
  margin-top: 40px;
  font-size: 22px;
  color: #888;
}
